/* routes-list */

const APP = 'app-customer'

import BaseAPP from './App.vue'
// function wrapper (loadComponent) {

//     window.__app__.init('app-customer')

//     return loadComponent

// }


export default [
{
    path: `/${APP}`,
    name: `${APP}_compensate`,
    component: BaseAPP,
    redirect: `/${APP}/questionSort2buyer`,
    children: [
        {
            path: 'questionSort2buyer',
            name: `${APP}_questionSort_buyer`,
            meta: {
                title: '买家问题分类'
            },
            props: {
                identity: 1
            },
            component: () => import(/* webpackChunkName: "QuestionSort" */ './views/QuestionSort.vue')
        },
        {
            path: 'test',
            name: `${APP}——test`,
            meta: {
                title: '买家问题分类'
            },
            props: {
                identity: 1
            },
            component: () => import(/* webpackChunkName: "test" */ './views/test.vue')
        },
        {
            path: 'questionSort2seller',
            name: `${APP}_questionSort_seller`,
            meta: {
                title: '卖家问题分类'
            },
            props: {
                identity: 2
            },
            component: () => import(/* webpackChunkName: "QuestionSort" */ './views/QuestionSort.vue')
        }
    ]
}
]



